/* target 1920 1080 D 20% oki */
/* target 1600x900 D 5% oki */
/* for root 100% */

/* target 1536x864 D 12% oki */
/* target 1440x900 D  9% oki */
/* target  1280x800 T 14% oki  */
@media only screen and (max-width: 1600px) {
  :root {
    font-size: 90%;
  }

  .right-section-attraction {
    transform: scale(0.9) translateX(45rem);
  }

  .background-text {
    margin: 0;
    font-size: 14.875rem;
  }
}

/* target 1366x768 D oki */
/* target 1280x1024 D oki */
/* target 1366x1024 T iPadPro oki */
/* target 1280x800 T 5% oki */
@media only screen and (max-width: 1400px) {
  :root {
    font-size: 80%;
  }
}

/* target 1024x600 D ?? */
/* target 1024x768 T 25% iPad */

@media only screen and (max-width : 1200px) {
  :root {
    font-size: 70%;
  }

  .right-section-attraction {
    transform: scale(0.9) translateX(43rem);
  }
}



@media only screen and (max-width :1020px) {

  :root {
    font-size: 60%;
  }

  .right-section-attraction {
    transform: scale(0.9) translateX(45rem);
  }
}

/* target 823x411 M Pixel 2XL */
@media only screen and (max-width : 880px) {

  :root {
    font-size: 55%;
  }
}

/* target 720x540 T Surface Duo oki */
/* target 736x414 M iPhone 6/7/8 Plus oki */
@media only screen and (max-width : 800px) {
  :root {
    font-size: 48%;
  }
}

/* target 667x375 M iPhone 6/7/8 oki */
@media only screen and (max-width : 700px) {
  :root {
    font-size: 44%;
  }
}

/* target 640x360 M 24% oki */
@media only screen and (max-width : 640px) {
  :root {
    font-size: 40%;
  }
}

/* target 568x320 M iPhone5 */
@media only screen and (max-width : 585px) {
  :root {
    font-size: 100%;
  }
}

/* ORIENTAYION VERTICAL */

/* target 800x1280 T 14% orientation vertical  oki*/
@media screen and (max-width: 1280px) and (orientation: portrait) {
  :root {
    font-size: 50%;
  }

  .clendaryHouerBookingBody {
    height: 65rem;
  }

  /* 
  .menu-right {
    display: none;
  } */


}

/* target 1024x768 T orientation vertical */
/* target 1024x1366 T iPadPro orientation vertical */
@media only screen and (max-width : 1200px) and (orientation: portrait) {
  :root {
    font-size: 68%;
  }

  .right-section-attraction {
    transform: scale(0.9) translateX(43rem);
  }
}

/* target 962x601 T 5% oki Orientation Veryical */
@media only screen and (max-width : 1020px) and (orientation: portrait) {

  :root {
    font-size: 40%;
  }

  .right-section-attraction {
    transform: scale(0.9) translateX(45rem);
  }

  .menu-right {
    display: none;
  }

  .menu-left {
    margin-left: 0rem;
  }
}

/* target 800x1280 T orientation vertical */
@media only screen and (max-width : 800px) and (orientation: portrait) {

  :root {
    font-size: 51%;
  }
}

/* target 768x1024 T iPad orientation vertical */
@media only screen and (max-width : 768px) and (orientation: portrait) {

  :root {
    font-size: 50%;
  }
}

/* target 601x962 T orientation vertical */
/* target 600x900 T orientation vertical */
@media only screen and (max-width : 605px) and (orientation: portrait) {

  :root {
    font-size: 40%;
  }
}

/* target 540x720 T surfacePro orientation vertical */
@media only screen and (max-width : 550px) and (orientation: portrait) {

  :root {
    font-size: 36%;
  }
}

/* target 360x640 M 24% */
@media only screen and (max-width : 415px) and (orientation: portrait) {

  :root {
    font-size: 55%;
  }

  .text-header h1 {
    font-size: 2.5rem;
    width: 15rem;
  }

  .navbar ul li h2 a {
    font-size: 2.5rem;
  }

  .btn-header {
    width: 15rem;
    font-size: 1.6rem;
  }

  .container {
    margin: 0 3rem;
  }

  .article div {
    width: 35rem;
  }

  .title-header {
    font-size: 2rem;
  }

  .text-header {
    padding-left: 55%;
    margin-left: 0rem;
  }

  .article .btn {
    margin-top: 9.25rem;
  }

  .background-text {
    font-size: 8.875rem;
    transform: translateY(2rem);
  }

  .description-attraction {
    margin: 1.25rem 9rem 3.875rem 04rem;
  }

  .right-section-attraction {
    display: none;
  }


}

